import { animate, state, style, transition, trigger } from "@angular/animations";
import { Component, Input, OnInit } from "@angular/core";

@Component({
  selector: "app-collapse-card",
  templateUrl: "collapse-card.component.html",
  animations: [
    trigger("collapseTrigger", [
      state("collapsed", style({
        opacity: 0,
        height: 0,
        padding: 0,
      })),
      state("expanded", style({
        opacity: 1,
      })),
      transition("* => *", animate(".3s ease-in")),
    ]),
  ],
})
export class CollapseCardComponent implements OnInit {

  public state: string;

  @Input() public heading: string;
  @Input() public loadingIndicator?: boolean = false;
  @Input() public infoIcon?: string = "";
  @Input() public color?: string = "dark";
  @Input() public textColor?: string = "inverse_text";
  @Input() public isCollapse?: boolean = false;
  @Input() public icon: string;

  constructor() {

  }

  public ngOnInit() {
    this.state = this.isCollapse ? "collapsed" : "expanded";
  }

  public toggleCollapse() {
    this.state = (this.state === "collapsed") ? "expanded" : "collapsed";
  }

}



