import { NgModule } from '@angular/core';
import { IonicModule } from 'ionic-angular';
import { CoursesComponent } from './courses';
import { CollapseCardModule } from '../collapse-card/collapse-card.module';

@NgModule({
    declarations: [
        CoursesComponent,
    ],
    imports: [
        IonicModule,
        CollapseCardModule
    ],
    exports: [
        CoursesComponent
    ]
})
export class CoursesModule { }


