import { NgModule } from '@angular/core';
import { IonicModule } from 'ionic-angular';
import { ExperienceComponent } from './experience';
import { CollapseCardModule } from '../collapse-card/collapse-card.module';

@NgModule({
    declarations: [
        ExperienceComponent,
    ],
    imports: [
        IonicModule,
        CollapseCardModule
    ],
    exports: [
        ExperienceComponent
    ]
})
export class ExperienceModule { }


