import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { HomePage } from './home';
import { AvatarModule } from '../../components/avatar/avatar.module';
import { ExperienceModule } from '../../components/experience/experience.module';
import { EducationModule } from '../../components/education/education.module';
import { SkillsModule } from '../../components/skills/skills.module';
import { ProjectsModule } from '../../components/projects/projects.module';
import { InterestsModule } from '../../components/interests/interests.module';
import { NavbarModule } from '../../components/navbar/navbar.module';
import { CoursesModule } from '../../components/courses/courses.module';

@NgModule({
    declarations: [
        HomePage
    ],
    imports: [
        IonicPageModule.forChild(HomePage),
        AvatarModule,
        ExperienceModule,
        EducationModule,
        SkillsModule,
        ProjectsModule,
        InterestsModule,
        NavbarModule,
        CoursesModule,
        InterestsModule
    ],
})
export class HomePageModule { }


